    line-height: 26px;
    font-weight: bold;
    text-align: center;
    margin: 160px 0 20px 0;
  }
  .recommendations__description {
    text-align: center;
    width: 162px;
    margin: 0 auto;
  }
  .recommendations__item-icon {
    position: absolute;
    top: 60px;
    left: 90px;
    width: 76px;
    height: 76px;
    fill: #81b3d2;
  }
}
@media (min-width: 1200px) {
  .recommendations__item {
    width: 400px;
  }
  .recommendations__item-icon {
    left: 160px;
    top: 60px;
  }
  .recommendations__description {
    width: 285px;
  }
